.comment {
	padding: 0 8px;
	background: rgba(47, 128, 237, 0.05);
	border-radius: 100px;
	margin-bottom: 80px;
}

.comment__title {
	font-weight: 700;
	font-size: 24px;
	text-align: center;
	margin: 0;
	margin-bottom: 50px;
	padding-top: 50px;
	color: #000000;
}

.comment__author {
	margin-bottom: 20px;
	padding: 40px 20px 20px 51px;
	border-radius: 10px;
	background: #FFFFFF;
	box-shadow: 0px 10px 30px rgba(47, 55, 237, 0.1);
}

.comment__author__title {
	font-size: 14px;
	font-weight: 600;
	margin: 0;
	margin-bottom: 23px;
	text-transform: uppercase;
}

.comment__author__descripion{
	margin: 0;
}

.comment__author-image {
	border-radius: 100%;
	margin-bottom: 25px;
}